import { faSpinner } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Link } from 'gatsby-plugin-react-i18next';
import React from 'react'

const defaultClassName = 'button-main inline-flex items-center';

function Children({
  children,
  icon: passedIcon,
  loading,
}) {
  const icon = loading ? faSpinner : passedIcon;

  return (
    <>
      {children}
      &nbsp;
      <FontAwesomeIcon
        className="text-sm"
        spin={loading}
        icon={icon}
      />
    </>
  )
}

export function IconLink({
  className = "",
  to,
  children,
  icon,
  loading = false,
}) {
  return (
    <Link
      className={`${defaultClassName} ${className}`}
      to={to}
    >
      <Children {...{ children, icon, loading }} />
    </Link>
  )
}

export function IconButton({
  className = "",
  children,
  icon,
  loading = false,
  ...rest
}) {
  return (
    <button
      className={`${defaultClassName} ${className}`}
      disabled={loading}
      {...rest}
    >
      <Children {...{ children, icon, loading, }} />
    </button>
  )
}
